import React from 'react';
import { Link } from 'react-router-dom';
import { auth } from '../../services/firebase';
import styles from './Header.module.css';

function Header() {
  return (
    <header className="header">
      <nav className="navbar navbar-expand-sm fixed-top navbar-dark bg-transparent">
        <Link className="navbar-brand" to="/">Ensemble</Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup">
          {auth().currentUser
            ? <div className="navbar-nav">
              <button className={styles.logOut} onClick={() => auth().signOut()}>Logout</button>
            </div>
            : <div className="navbar-nav">
              <Link className="nav-item nav-link mr-3" to="/login">Log In</Link>
              <Link className="nav-item nav-link mr-3" to="/signup">Sign Up</Link>
            </div>}
        </div>
      </nav>
    </header>
  );
}

export default Header;